
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'

import {withdrawlog} from '../config'
import VScroll from '../components/VScroll'
export default {
  name: 'record',
  head: {
    title: {
      inner: ''
    }
  },
  data () {
    return {
      height: '0px',
      list: [],
      form: {
        userId: '',
        limit: 10,
        pageIndex: 0
      }
    }
  },
  components: {
    Cell,
    dateFormat,
    Group,
    VScroll
  },
  created () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.getList(() => {}, null)
  },
  methods: {
    statusNoMore () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'block'
      })
    },
    statusLoad () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'block'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    statusInit () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    handleSave (item) {
      this.$localStorage.set('record', JSON.stringify(item))
    },
    onRefresh (done) {
      this.form.pageIndex = 0
      this.statusInit()
      this.getList(done, 1)
    },
    onInfinite (done) {
      if (this.list.length % this.form.limit) {
        this.statusNoMore()
      } else {
        this.$el.querySelectorAll('.load').forEach(el => {
          el.style.display = 'block'
        })
        this.getList(done, 0)
      }
    },
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: withdrawlog,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        res.body.data.scoreList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.scoreList.length < This.form.limit) {
          this.statusNoMore()
        } else {
          this.statusLoad()
        }
        done()
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
      })
    }
  }
}



